@import url(https://fonts.googleapis.com/css2?family=Quantico:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?&family=Fascinate+Inline&family=Fascinate&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Reggae+One&display=swap);

* {  
  margin: 0; padding: 0;

  color: #f1f1f1;
  font-family: 'Quantico', sans-serif;
  font-size: 16px;

  border: none;
  list-style-type: none;
  outline: none;

  scroll-behavior: smooth;
}

body{
  background: #181818;
}

a {
  color: lightseagreen;
  font-size: .8rem;
}

em {
  color: #ffcb1f;
  /* font-family: 'Fascinate Inline', cursive; */
}

/* Header */
header {
  position: fixed;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;

  background: #353535;
  color: #a7a7a7;
  
  font-size: .8rem;
  text-align: center;

  box-shadow: 0 3px 10px black;
  z-index: 1;
}

header h3 {
  width: 50px;

  /* font-family: 'Fascinate', cursive; */
  font-size: .8rem;

  text-shadow: 1px 2px 2px rgba(0, 0, 0, 0.5);
}

header h2 {
  position: relative;
  top: 0;
  left: -20px;  

  color: #ffcb1f;
  font-size: 3rem;
  /* font-family: 'Fascinate Inline', cursive; */

  text-shadow: 2px 3px 2px rgba(0, 0, 0, 0.85);

  z-index: -1;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* Navigation Buttons */

.navButtonLeft,
.navButtonRight {
  position: fixed;

  margin: 0;
  padding-right: 35px;
  padding-bottom: 12px;


  font-size: 1.6rem;
  font-weight: lighter;
  font-family: 'Reggae One', cursive;

  top: calc(50vh - 50px);
  right: -40px;

  width: 80px;
  height: 70px;

  border-radius: 40px;
  background: rgba(37, 37, 37, 0.856);
  color: white;

  transform:skew()
}

.navButtonLeft {
  left: -40px;
  padding-right: 18px;
  padding-bottom: 12px;

  text-align: right;
}

/* Search expander button */
.searchExpanderBtn {
  position: fixed;
  top: 50px;
  right: -16px;
  
  padding: 0 10px 0 0;

  width: 60px;
  height: 45px;

  background-color: #484848;
  border-radius: 35px;
  color: #f1f1f1;

  font-size: 1rem;

  box-shadow: 0 0 5px 4px rgba(0, 0, 0, 0.418);
  opacity: .75;
  z-index: 1;
}

.gameList{
  display: flex;
  flex-direction: column;
  align-items: center;

  margin: 60px 0;

  animation: fadeIn 1s;
}

.gameList__item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  margin: 10px 0;
  width: 100%;

  box-shadow: -2px 5px 6px .2px #0000003a;

  text-align: center;
}

.gameList__item h3 {
  text-shadow: 1px 3px 2px rgb(0, 0, 0, 1);
}

.gameList__item img {
  border-radius: 2px;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, .8);
  
  height: 50px;
  width: 150px;

  object-fit: cover;
}

.gameList__btn {
  margin: 30px 0 15px 0;
  padding: 5px 20px;

  border-radius: 4px;
  color: #181818;
}

.ex--price {
  font-size: .8rem;
  text-decoration: line-through;
  
  opacity: .5;
}

.actual--price {
  margin: -6px 0;
  font-size: 1.6rem;
}

.actual--price::after {
  content: '€';

  font-size: .6rem;
}

.gameList__item--naming {
  text-align: left;
}

.gameList__item--info {
  display: flex;
  flex-direction: column;
  align-self: center;

  padding: 0 10px;
}

.gameList__item--anim {
  position: relative;
  left: 0;

  display: flex;
  align-items: center;

  width: 100%;

  transform: translate(22px, 26px);

  opacity: .75;
}

/* .shape {
  animation: spiral 2.5s linear infinite alternate;
} */

.shape img {
  width: 16px;
  height: 16px;

  box-shadow: 0 0 0;
}

/* Status page bottom */
.pageFooter {
  position: fixed;
  bottom: -1px;

  display: flex;
  justify-content: space-around;
  align-items: center;
  
  width: 100%;
  height: 50px;

  background: #353535;
  color: #a7a7a7;

  box-shadow: 0 -3px 10px black;

  z-index: 1;
}

.pageFooter--text {  
  font-size: .8rem;
}

.pageFooter--status {
  display: flex;
  align-items: center;
}

.pageFooter--status p:nth-child(1) {
  font-size: .7rem;
  text-shadow: 1px 2px 2px rgba(0, 0, 0, .5);
}
.pageFooter--status p:nth-child(2) {
  color: #ffcb1f;

  font-size: 1.75rem;
  /* font-family: 'Fascinate Inline', cursive; */
  font-weight: bold;
  text-shadow: 1px 2px 2px rgba(0, 0, 0, .75);

  transform: translate(2px, -3px);
}


@keyframes fadeIn {
  0%{
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}


@media (min-width: 676px) {
  .gameList{
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .gameList__item {
    display: flex;
    flex-direction: row;
  
    margin: 10px 60px;
    width: 250px;
  
    box-shadow: -2px 5px 6px .2px #0000003a;
  
    text-align: center;
  }
  
  .gameList__item--anim {
    position: relative;
    left: 0;
  
    display: flex;
    align-items: center;
  
    width: 100%;
  
    transform: translate(20px, 25px);
  
    opacity: .75;
  }
}
.search {
  top: 0;
  bottom: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
  
  margin: 60px 0;
  width: 100%;

  animation: .5s fadeIn ;
}

input {
  margin: 0 10px 0 0;
  padding: 3px 10px;

  border-radius: 3px;
  background-color: #484848;
  color: #f1f1f1;
  
  font-size: .75rem;
}

button {
  margin: 10px 0 30px;
  padding: 3px 10px;

  border-radius: 4px;
  color: #484848;

  font-size: .7rem;
  font-weight: bold;
}

button:active {
  box-shadow: inset 0 0 2px 1px rgba(0, 0, 0, 0.6);
}

.search .gameList {
  margin: 0;

  animation: fadeIn 1s;
}


@keyframes fadeIn {
  0%{
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}


/* Disable store icon into the Search Section */
.search .gameList__item--anim {
  display: none;
}
.loader {
  position: fixed;
  top: 50px;
  left: 0;

  width: 100%;
  height: 100vh;
  background: rgba(0,0,0,.75);

  z-index: 1;
}

.loader__anim {
  display: flex;
  justify-content: center;
  align-items: center;
  
  width: 100%;
  height: 100%;
}

.loader__anim h2 {    
  color: #ffcb1f;

  font-size: 1.5rem;

  letter-spacing: 5px;

  transform: translateY(-75px);
}

@media (min-width: 676px) {
  .loader__anim h2 {
    font-size: 2.5rem;
  }
}
